.slides .slick-prev,
.slides .slick-next {
  background-color: transparent;
}

.slides .slick-prev,
.slides .slick-next {
  position: absolute;
  top: 50%;
  z-index: 1;
}

.slides .slick-prev {
  left: 5%;
}

.slides .slick-next {
  right: 5%;
}
button.slick-arrow.slick-prev::before {
  content: "<" !important;
  font-size: -webkit-xxx-large;
}
button.slick-arrow.slick-next::before {
  content: ">" !important;
  font-size: -webkit-xxx-large;
}
.slick-dots li button:before {
}
